(() => {
    const PREFIX = "experiment_";

    const AVAILABLE_EXPERIMENTS = [
        {
            name: "cta",
            variants: [
                {
                    name: "with_monthly_cost",
                    data: {
                        text: "Custom built and fully installed for as little as $289k or $1,730/mo."
                    }
                },
                {
                    name: "with_installation_cost",
                    data: {
                        text: "Custom units start at $192k, plus $97k base installation cost."
                    }
                }
            ]
        }
    ];

    const experiments = {};

    function start() {
        AVAILABLE_EXPERIMENTS.forEach((experiment) => {
            const key = PREFIX + experiment.name;

            const variantName =
                localStorage.getItem(key) ||
                experiment.variants[
                    Math.trunc(Math.random() * experiment.variants.length)
                ].name;

            localStorage.setItem(key, variantName);

            experiments[experiment.name] = experiment.variants.find(
                ({ name }) => name === variantName
            );

            if (typeof gtag !== "undefined") {
                gtag("event", `${key}_${variantName}`);
            }
        });
    }

    globalThis.experimentValueFor = function experimentValueFor(
        experimentName,
        key
    ) {
        return experiments[experimentName].data[key];
    };

    start();
})();
