async function createApplication(name) {
    const [destroyStyle, destroyScript] = await Promise.all([
        loadStyles(`/assets/stylesheets/${name}/bundle.css`),

        import(`/assets/javascripts/${name}/bundle.js`)
            .then((module) => module.default())
            .catch((error) => {
                if (typeof Sentry === "undefined") {
                    console.error(error);
                } else {
                    Sentry.captureException(error);
                }
            })
    ]);

    return function destroyApplication() {
        if (destroyScript) {
            destroyScript();
        }

        if (destroyStyle) {
            destroyStyle();
        }
    };
};
