(() => {
    function createSubscription(articleElement) {
        if (!articleElement) {
            return;
        }

        const subscriptionElement =
            articleElement.querySelector(".subscription");

        if (!subscriptionElement) {
            return;
        }

        const form = subscriptionElement.querySelector("form");

        function didSubmitForm(e) {
            e.preventDefault();

            const input = form.querySelector("input[type=email]");
            const email = input.value;
            input.value = "";
            input.disabled = true;

            API.subscribe(email, ["about"]);

            subscriptionElement.classList.add("successful");

            setTimeout(
                () => subscriptionElement.classList.add("disposed"),
                2000
            );

            setTimeout(() => {
                input.disabled = false;
                subscriptionElement.classList.remove("successful", "disposed");
            }, 3000);
        }

        function isVisible() {
            const rect = input.getBoundingClientRect();
            return window.innerHeight > rect.bottom;
        }

        function didChangeViewport() {
            if (!isVisible()) {
                input.blur();
            }
        }

        function didFocus() {
            window.addEventListener("scroll", didChangeViewport, {
                passive: true
            });
        }

        function didBlur() {
            window.removeEventListener("scroll", didChangeViewport);
            input.classList.toggle("filled", input.value.length);
        }

        function didPressKey(e) {
            if (document.body.querySelector(".modal.visible")) {
                return;
            }

            const char = String.fromCharCode(e.keyCode);

            if (isVisible() && /[a-zA-Z0-9]/.test(char)) {
                input.focus();
            }
        }

        const input = form.querySelector("input[type=email]");
        input.disabled = false;
        subscriptionElement.classList.remove("successful", "disposed");
        form.addEventListener("submit", didSubmitForm);

        input.addEventListener("focus", didFocus);
        input.addEventListener("blur", didBlur);
        window.addEventListener("keydown", didPressKey);
    }

    window.addEventListener("router:did-update-content", (e) => {
        createSubscription(
            document.querySelector("article.current, article.single")
        );
    });

    document.addEventListener("DOMContentLoaded", () => {
        createSubscription(
            document.querySelector("article.current, article.single")
        );
    });
})();
