Backyard.createCardsScroll = function createCardsScroll(element) {
    const ANIMATION_INTERVAL = 7000;
    const ANIMATION_DURATION = 1000;
    const NUMBER_OF_CARDS = 5;

    const items = Array.from(element.querySelectorAll(".item"));
    const scroll = element.querySelector(".scroll");
    const previous = Array.from(element.querySelectorAll(".previous"));
    const next = Array.from(element.querySelectorAll(".next"));
    const indicators = Array.from(element.querySelectorAll(".indicators div"));

    let indexForCurrentCard = 0;
    let shouldAnimate = false;
    let interval;

    function changeIndex(direction) {
        indexForCurrentCard = Math.max(
            Math.min(indexForCurrentCard + direction, items.length - 1),
            0
        );

        element.querySelector(".paginated-scroll").dispatchEvent(
            new CustomEvent("progress:set", {
                detail: {
                    progress: indexForCurrentCard / (NUMBER_OF_CARDS - 1)
                }
            })
        );

        previous.forEach(
            (button) => (button.disabled = indexForCurrentCard === 0)
        );

        next.forEach(
            (button) =>
                (button.disabled = indexForCurrentCard === NUMBER_OF_CARDS - 1)
        );

        updateLayout();
    }

    function offsetForCardAt(index) {
        return -(
            items[index].getBoundingClientRect().left -
            items[0].getBoundingClientRect().left
        );
    }

    function updateLayout() {
        const tx = offsetForCardAt(indexForCurrentCard);
        scroll.style.transform = `translateX(${tx}px)`;
    }

    function updateLayoutProgress(progress) {
        const tx =
            -progress * (NUMBER_OF_CARDS - 1) * (items[0].clientWidth + 24.0);
        scroll.style.transform = `translateX(${tx}px)`;
        indexForCurrentCard = Math.round(progress * (NUMBER_OF_CARDS - 1));

        indicators.forEach((indicator, i) => {
            indicator.classList.toggle("current", i === indexForCurrentCard);
        });
    }

    previous.forEach((button) => {
        button.addEventListener("click", function didClickPreviousCardButton() {
            changeIndex(-1);
            shouldAnimate = false;
            clearInterval(interval);
        });
    });

    next.forEach((button) => {
        button.addEventListener("click", function didClickNextCardButton() {
            changeIndex(1);
            shouldAnimate = false;
            clearInterval(interval);
        });
    });

    function viewportDidChange() {
        if (!shouldAnimate) {
            return;
        }

        const { top, bottom } = scroll.getBoundingClientRect();

        const isOffscreen =
            bottom < 0.0 ||
            top > window.innerHeight ||
            document.visibilityState !== "visible";

        if (isOffscreen) {
            clearInterval(interval);
            interval = undefined;
        } else if (interval === undefined) {
            interval = setInterval(changeIndex, ANIMATION_INTERVAL, 1);
        }
    }

    function didChangeProgress(e) {
        scroll.classList.add("without-animation");

        setTimeout(() => {
            scroll.classList.remove("without-animation");
        }, 10);

        shouldAnimate = false;
        clearInterval(interval);
        updateLayoutProgress(e.detail.progress);
    }

    window.addEventListener("scroll", viewportDidChange, {
        passive: true
    });

    document.addEventListener("visibilitychange", viewportDidChange);

    element
        .querySelector(".paginated-scroll")
        .addEventListener("progress", didChangeProgress);

    viewportDidChange();
    changeIndex(0);
    updateLayoutProgress(0.0);

    return function destroyCardsScroll() {
        window.removeEventListener("scroll", viewportDidChange);
    };
};
