function createVideo(url) {
    const video = document.createElement("video");
    video.controls = true;

    if (video.canPlayType("application/vnd.apple.mpegURL")) {
        video.src = url + ".m3u8";
        video.load();
    } else {
        const hls = new Hls({
            enableWorker: false,
            lowLatencyMode: true,
            backBufferLength: 90,
            maxMaxBufferLength: 1,
            startLevel: 0
        });

        hls.attachMedia(video);

        hls.on(Hls.Events.MEDIA_ATTACHED, () => {
            hls.loadSource(url + ".m3u8");
        });
    }

    video.play();

    return video;
}

Backyard.createModalPresenters = function createModalPresenters() {
    const isMobile =
        window.matchMedia("only screen and (max-width: 768px)").matches ===
        true;

    function displayVideoFullscreen(url) {
        const video = createVideo(url);
        video.width = window.innerWidth;
        video.height = window.innerHeight;
        video.style.position = "absolute";
        video.style.left = "-10000vw";

        document.body.appendChild(video);

        video.addEventListener(
            "webkitendfullscreen",
            function didExitFullscreen() {
                video.parentElement?.removeChild(video);
            },
            { once: true }
        );
    }

    document.querySelectorAll("*[data-modal]").forEach((element) => {
        const isImage = element.dataset.modal.includes("images");
        const isVideo = element.dataset.modal.includes("videos");

        if (isImage && isMobile) {
            return;
        }

        element.addEventListener("click", function didClickModalPresenter() {
            if (isVideo && isMobile) {
                displayVideoFullscreen(element.dataset.modal);
                return;
            }

            Backyard.presentModal(
                element,
                element.dataset.modal,
                element.dataset.modalAspectRatio
            );
        });
    });

    return function destroyModalPresenters() {};
};

Backyard.presentModal = function presentModal(sender, url, aspectRatio) {
    const element = document.createElement("div");
    element.classList.add("backyard-modal");

    const wrapper = document.createElement("div");
    wrapper.classList.add("wrapper");

    const content = document.createElement("div");
    content.classList.add("content");

    const button = document.createElement("button");
    content.appendChild(button);

    wrapper.appendChild(content);
    element.appendChild(wrapper);
    document.body.appendChild(element);

    function dismiss() {
        document.body.style.overflow = null;

        element.classList.remove("visible");

        setTimeout(() => {
            element.parentElement.removeChild(element);
        }, 200);

        window.removeEventListener("keydown", didPressKey);
    }

    function displayVideo() {
        video = createVideo(url);
        content.appendChild(video);
    }

    function displayImage() {
        const image = document.createElement("img");
        image.src = url;

        content.appendChild(image);
    }

    function displayYoutube() {
        const iframe = document.createElement("iframe");
        iframe.width = "100%";
        iframe.height = "100%";
        iframe.src = url + "?autoplay=1&rel=0&modestbranding=1&playsinline=0";
        iframe.frameBorder = 0;
        iframe.allow =
            "autoplay; encrypted-media; picture-in-picture; fullscreen";
        iframe.allowFullScreen = "allowFullScreen";

        content.appendChild(iframe);
    }

    function didPressKey(e) {
        switch (e.key) {
            case "Escape":
                dismiss();
                break;

            default:
                break;
        }
    }

    let isMouseDown = false;

    function didMouseDown(e) {
        if (e.target !== element) {
            return;
        }

        isMouseDown = true;
    }

    function didMouseUp(e) {
        dismiss();

        isMouseDown = false;
    }

    function didClickCloseButton() {
        dismiss();
    }

    function didChangeFullscreen() {
        document.body.classList.toggle(
            "webkit-fullscreen",
            document.webkitIsFullScreen
        );
    }

    button.addEventListener("click", didClickCloseButton);
    element.addEventListener("mousedown", didMouseDown);
    element.addEventListener("mouseup", didMouseUp);
    window.addEventListener("keydown", didPressKey);
    document.addEventListener("webkitfullscreenchange", didChangeFullscreen);

    document.body.style.overflow = "hidden";

    content.style.aspectRatio = aspectRatio;

    if (url.includes("video")) {
        displayVideo();
    }

    if (url.includes("images")) {
        displayImage();
    }

    if (url.includes("youtube")) {
        displayYoutube();
    }

    setTimeout(() => {
        element.classList.add("visible");
        content.style.transform = null;
    });
};
