Backyard.createPaginatedScroll = function createPaginatedScroll(selector) {
    function create(element) {
        const numberOfPages =
            parseInt(element.dataset.pages) || Number.MAX_SAFE_INTEGER;
        let isDragging = false;
        let progress = parseFloat(element.dataset.progress) || 0.0;
        let initialProgress = progress;
        let previousProgress = progress;
        let initialPointer = { x: 0, y: 0 };
        let isAnimating = false;
        let isScrolling = false;
        let isMouseDown = false;
        let acceleration = 0.0;
        let strengthRatio = [
            parseFloat(element.dataset.minRatio) || 0.33,
            parseFloat(element.dataset.maxRatio) || 0.8
        ];

        function lim(n, min = 0.0, max = 1.0) {
            return Math.min(Math.max(n, min), max);
        }

        function pointer(e) {
            if (e.type.startsWith("touch")) {
                return {
                    x: e.changedTouches[0].clientX,
                    y: e.changedTouches[0].clientY
                };
            }

            return {
                x: e.clientX,
                y: e.clientY
            };
        }

        function animateProgress(to) {
            isAnimating = true;

            function update() {
                if (isAnimating === false) {
                    return;
                }

                progress += (to - progress) * 0.1;
                updateProgress();

                if (Math.abs(progress - to) < 0.0001) {
                    progress = to;
                    updateProgress();
                    isAnimating = false;
                }

                if (isAnimating === true) {
                    requestAnimationFrame(update);
                }
            }

            update();
        }

        function updateProgress() {
            const event = new CustomEvent("progress", { detail: { progress } });
            element.dispatchEvent(event);
        }

        function didStartDrag(e) {
            if (isDragging === true || isScrolling === true) {
                return;
            }

            isMouseDown = true;
            initialPointer = pointer(e);
        }

        function didDrag(e) {
            if (isScrolling === true) {
                return;
            }

            const { x, y } = pointer(e);

            const ratio =
                progress < 0.0 || progress > 1.0
                    ? strengthRatio[0]
                    : strengthRatio[1];

            const dx = (initialPointer.x - x) * ratio;
            const dy = initialPointer.y - y;

            if (isDragging === true) {
                if (e.type.startsWith("touch") === true) {
                    e.stopPropagation();
                    e.preventDefault();
                }

                progress = initialProgress + dx / element.clientWidth;
                acceleration = progress - previousProgress;
                previousProgress = progress;
                updateProgress();
            } else if (isMouseDown && Math.abs(dx) * 2.0 > Math.abs(dy)) {
                if (e.type.startsWith("touch") === true) {
                    e.stopPropagation();
                    e.preventDefault();
                }

                isAnimating = false;
                isDragging = true;
                initialPointer = pointer(e);
                initialProgress = progress;
                previousProgress = progress;
                acceleration = 0.0;
            } else if (isMouseDown) {
                isScrolling = true;
                isDragging = false;
            }
        }

        function didEndDrag(e) {
            isScrolling = false;
            isMouseDown = false;

            if (isDragging === false) {
                return;
            }

            isDragging = false;

            const n = numberOfPages - 1;
            const currentPage = Math.round(progress * n);

            if (acceleration > 0) {
                animateProgress(lim((currentPage + 1) / n));
            } else if (acceleration < 0) {
                animateProgress(lim((currentPage - 1) / n));
            } else {
                animateProgress(lim(currentPage / n));
            }
        }

        function didSetProgress(e) {
            progress = e.detail.progress;
            isDragging = false;
        }

        element.addEventListener("touchstart", didStartDrag);
        element.addEventListener("touchmove", didDrag);
        element.addEventListener("touchend", didEndDrag);

        // element.addEventListener("mousedown", didStartDrag);
        // element.addEventListener("mousemove", didDrag);
        // element.addEventListener("mouseup", didEndDrag);
        // element.addEventListener("mouseleave", didEndDrag);

        element.addEventListener("progress:set", didSetProgress);

        return function destroy() {};
    }

    const instances = Array.from(document.querySelectorAll(selector)).map(
        create
    );

    return function destroyPaginatedScroll() {
        instances.forEach((destroy) => {
            destroy();
        });
    };
};
