document.addEventListener("DOMContentLoaded", () => {
    document.body.classList.remove("no-javascript");
});

window.addEventListener("message", (e) => {
    switch (e.data.action) {
        case "reload":
            location.reload();
            break;

        case "openRoute":
            openRoute(e.data.route);
            break;

        default:
            break;
    }
});
